.annotation-wrapper {
  margin-bottom: $spacing-unit/2;
}

.annotation {
  display: inline-block;
  @include relative-font-size(0.9);
  padding-left: 2em; // have to figure out how to prevent it from running over on the right too...
  @include x-box-sizing(border-box);
  color: $grey-color-dark;

    br {
      font-size: 0px;
    }
}

span.annotation {
  padding: $base-font-size 0 $base-font-size 2em;
}

.annotation-link {
  &:hover {
    cursor: pointer;
  }
  @include relative-font-size(0.9);
  border: 1px solid $brand-color;
  border-radius: 3px;
  background-color: $footnote-link-color;
  padding: 0 $base-font-size/4;
  min-width: $base-font-size - 3px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  line-height: $base-line-height;
}


// Adjusting footnote placement for headings
h2 {
  .annotation-link {
    position: relative;
    bottom: $base-font-size * 2 / 6;
  }
}

h3 {
  .annotation-link {
    position: relative;
    bottom: $base-font-size * 1.625 / 6;
  }
}


// Functional classes
.annotation-link-selected {
  &:before {
    content: "âœ•";
    display: inline-block;
    margin: 0 $base-font-size/3 0 0;
    opacity: 1;
    vertical-align: top;
  }

  background-color: $grey-color-light;

}

// Uses the max-height hack: http://bit.ly/2mpGSwW
.annotation {
  display: none;
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition:
    max-height .3s ease-out 0s,
    width .01s ease-out .3s,
    opacity .2s ease-out 0s;
  width: 0;
}

.annotation-expanded {
  max-height: 5000px;
  width: 100%;
  opacity: 1;
  transition:
    max-height .5s ease-in .01s,
    width .01s ease-in 0s,
    opacity .2s ease-in-out .21s;
}

div > .annotation-expanded {
  margin-top: $spacing-unit/2;
}

